import React from 'react'
import logo from './logo.svg';
import './App.css';
import Header from './components/Header';
import Tiles from './components/Tiles';
import { useState, useEffect } from 'react'
import { BrowserRouter as Router, Route, HashRouter} from 'react-router-dom'

const App = () => {

  const [tiles, setTiles] = useState(() => {
    return []
  })
  
  const fetchTiles = async () => {
    const result = await fetch('https://www.khanacademy.org/api/internal/user/scratchpads?username=grantjenkins&callback=?')
    const data = await result.json()
    console.log(data)
    return data.scratchpads
  }
  
  useEffect(() => {
    const getTiles = async () => {
      const tilesFromServer = await fetchTiles()
      setTiles(tilesFromServer)
    }
  
    getTiles()
  }, [])

  return (
    <React.StrictMode>
      <HashRouter>
        <div className="App">
          <Header title="Wowzers"/>
          {tiles.length > 0 ? <Tiles tiles={tiles} /> : "No tiles to show"}
            
        </div>
      </HashRouter>
    </React.StrictMode>
  );
}

export default App;
