import PropTypes from 'prop-types'

import Tile from "./Tile"

const Tiles = ({tiles}) => {
    return (
        <>
            {tiles.map((tile, index) => (
                <Tile key={index} tile={tile} /> 
            ))}
        </>
    )
}

Tile.defaultProps = {
    tiles: [
        {
            tile: "Foo",
            index: 1
        },
        {
            tile: "Bar",
            index: 2
        }
    ]
}

export default Tiles
